.foundations{-webkit-box-shadow:0px 0px 30px 0px rgba(82,63,105,0.05);box-shadow:0px 0px 30px 0px rgba(82,63,105,0.05);border:0;border-radius:0.42rem;padding:1.5rem 2rem;background:#fff}.foundations h5 a{color:#474747;transition:all .3s ease}.foundations h5 a:hover{color:#fd7e14}.foundations .smalltext-item-foundation{color:#acacac}.card-library>a{position:relative;height:280px}.card-library>a img{height:100%;object-fit:cover}.card-library>a:before{content:'';position:absolute;top:15px;left:15px;right:15px;bottom:15px;border-top:5px solid rgba(255,255,255,0.6);border-left:5px solid rgba(255,255,255,0.6);z-index:3;perspective:100px;transition:all .3s ease;transform:scale(0.3);opacity:0}.card-library:hover>a:before{transform:scale(1) rotate(0);opacity:1}.card-library>a:after{content:'';position:absolute;bottom:0;left:0;width:100%;height:100%;transition:all .5s ease;background:rgba(247,125,20,0.47);z-index:1;visibility:visible;opacity:0}.card-library:hover>a:after{opacity:1}.card-library:hover h5{position:relative;z-index:2}.card-library:hover h5 a{color:#f77d14}
